<template>
  <div class="client-login">
    <div class="login">
      <div class="name">美瑞克会员登录</div>
      <div class="register">注册</div>
    </div>

    <div class="telnum">
      <div class="select">
        <select>
          <option>+86</option>
        </select>
      </div>
      <div class="iphonenum">
        <input type="text" name="" placeholder="请输入您的手机号">
      </div>
    </div>

    <div class="comfirm">
      <div class="comfirm-num">
        <input type="text" name="" placeholder="请输入收到的验证码">
      </div>
      <div class="get-num">
        <input type="button" name="" value="获取验证码">
      </div>
    </div>

    <div class="inputs">
      <input type="button" name="" value="登陆" @click="login">
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return{

      }
    },
    mounted() {
      this.$nextTick(() => {
        $(".telnum .iphonenum input").click(function(){
          $(".telnum .iphonenum input").css({
            borderBottom: '1px solid #edaf2c',
            outline: 'none'
          });
          $(".comfirm .comfirm-num input").css({
            border: 'none',
            outline: 'none'
          });

        });
        $(".comfirm .comfirm-num input").click(function(){
          $(".comfirm .comfirm-num input").css({
            borderBottom: '1px solid #edaf2c',
            outline: 'none'
          });
          $(".telnum .iphonenum input").css({
            border: 'none',
            outline: 'none'
          });

        });
      })
    },
    methods: {
      login() {
        this.axios.post('/client/login', {
          userName: 'fujianbin',
          password: '123456'
        }).then(res => {
          this.$store.commit('changeLoginStatus', true);
          console.log(this.$router)
          this.$router.push({path: 'Home'})
        })
      }
    }
  }
</script>

<style>
  @import "../../assets/css/client/Client_login.css";
  @import "../../assets/css/client/reset.css";
</style>



